body {
  background-color: #000;
}
.container {
  /* height: 100vh;
  width: 100%; */
  background-image: url("../../public/bg.png");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #aaa;
}

.nav {
  height: 50px;
  width: 90%;
  margin: 0.5rem auto;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  border: 1px solid #333;
  box-shadow: 0px 3px 20px rgba(255,255,255,0.1);
}

.nav ul {
  display: flex;
  justify-content: flex-end;
  /* border: 1px solid red; */
}

.nav li {
  border: 1px solid red;
  list-style: none;
  /* margin: 0.5rem;
  padding: 0.5rem; */
}

.controls {
  width: 5rem;
  height: 100%;
  border: 1px solid red;
}
.main {
  height: 100vh;
  width: 100%;
  /* max-width: 800px;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  background-color: #333; */
}

.card {
  height: 500px;
  width: 300px;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  border-radius: 0.5rem;
  overflow: hidden;
}

.image {
  height: 200px;
  width: 100%;
  background-image: url('../../public/web.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  filter: grayscale(100%);
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  padding: 0.5rem;
  background-image: linear-gradient(30deg, #211010, #5e2222);
}

